.footer {
  background-color: black;
  padding-bottom: 104px;
  padding-top: 100px;
}

.footer__container {
  padding-right: 170px;
  padding-left: 80px;

  h2 {
    font-style: normal;
    font-weight: normal;
    font-size: 90px;
    line-height: 100px;
    /* identical to box height, or 111% */


    color: rgba(255, 255, 255, 0.4);
  }
}

.footer__link {
  font-style: normal;
  font-weight: 300;
  font-size: 90px;
  line-height: 100px;
  /* identical to box height, or 111% */
  padding-bottom: 1px;
  border-bottom: 1px solid rgba(255, 255, 255, 0.4);
  color: #FFFFFF;
  cursor: pointer;
  -webkit-transition: margin-left 250ms ease-out;
  -moz-transition: margin-left 250ms ease-out;

  &:hover {
    margin-left: 20px;
  }
}

.footer__top-block {
  display: flex;
  justify-content: space-between;
}

.footer__link-style {
  position: relative;
  right: 62px;
  font-style: normal;
  font-weight: normal;
  font-size: 22px;
  line-height: 60px;
  border: 1px solid rgba(72, 109, 146, 0.2);
  border-radius: 52px;
  padding: 10px;
  padding-right: 80px;
  padding-left: 80px;
  box-sizing: border-box;
  color: transparent;
  -webkit-background-clip: text;
  background-clip: text;
  background-image: linear-gradient(to right, red, fuchsia);
}

.footer__link-mob {
  padding-top: 109px;

  p {
    font-style: normal;
    font-weight: normal;
    font-size: 16px;
    line-height: 20px;
    text-transform: lowercase;
    padding-bottom: 5px;
    padding-left: 2px;
    color: rgba(255, 255, 255, 0.4);
  }

   a {
     font-style: normal;
     font-weight: normal;
     font-size: 22px;
     line-height: 34px;
   }
}


.footer__link-addr {
  padding-top: 73px;
  padding-right: 40px;

  p {
    font-style: normal;
    font-weight: normal;
    font-size: 16px;
    line-height: 20px;
    text-transform: lowercase;
    padding-left: 1px;
    padding-bottom: 5px;
    color: rgba(255, 255, 255, 0.4);
  }
}

.footer__addr-link {
  font-style: normal;
  font-weight: normal;
  font-size: 22px;
  line-height: 34px;
  padding-top: 5px;

  &:hover {
    color: transparent;
    -webkit-background-clip: text;
    background-clip: text;
    background-image: linear-gradient(to right, red, fuchsia);
  }
}

.footer__contacts-wrapper {
  display: flex;
  justify-content: space-between;
}


.footer__list-option {
  li {
    padding-bottom: 23px;
    padding-right: 12px;
    font-style: normal;
    font-weight: normal;
    font-size: 22px;
    line-height: 34px;
  }
}


.footer__socials {
  display: flex;
  justify-content: space-between;
  padding-top: 243px;
  width: 100%;
  max-width: 696px;
}


.footer__list-social {
  display: flex;
  flex-direction: column;
  max-height: 254px;
  flex-wrap: wrap;
  width: 100%;
  justify-content: space-between;
  max-width: 423px;

  li {
    width: 274px;
    padding-bottom: 25px;
    padding-right: 12px;
    font-style: normal;
    font-weight: normal;
    font-size: 22px;
    line-height: 34px;
  }
}


.footer__copyright {
  max-width: 455px;
  position: relative;
  top: -44px;

  p {
    font-style: normal;
    font-weight: normal;
    font-size: 16px;
    line-height: 20px;
    color: rgba(255, 255, 255, 0.4);
    padding-bottom: 20px;
  }

  a {
    font-style: normal;
    font-weight: normal;
    font-size: 16px;
    line-height: 20px;
    color: rgba(255, 255, 255, 0.4);
  }
}

.footer__brief-link {
  display: flex;
  align-items: flex-end;
}

@media (max-width: 1220px) {
  .footer__container {
    padding-right: 80px;
  }

  .footer__top-write {
    h2 {
      font-size: 70px;
    }

    a {
      font-size: 70px;
    }
  }
}

@media (max-width: $tablet-width) {
  .footer__container {
    padding-left: 20px;
    padding-right: 20px;
  }

  .footer__top-write {
    h2 {
      font-size: 50px;
    }

    a {
      font-size: 50px;
    }
  }
}

@media (max-width: $mobile-width) {
  .footer__top-write {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding-left: 8px;

    h2 {
      font-style: normal;
      font-weight: normal;
      font-size: 38px;
      line-height: 43px;
      width: 100%;
    }

    a {
      font-style: normal;
      font-weight: 300;
      font-size: 38px;
      line-height: 43px;
      text-align: left;
      width: 100%;
      max-width: 266px;
    }
  }

  .footer__top-block {
    flex-direction: column;
    align-items: center;
  }

  .footer__brief-link {
    padding-top: 72px;
    font-style: normal;
    font-weight: 300;
    font-size: 38px;
    line-height: 43px;
    display: flex;
    justify-content: center;
  }

  .footer__link-style {
    right: 0;
    font-style: normal;
    font-weight: normal;
    font-size: 22px;
    line-height: 60px;
    padding-left: 70px;
    padding-right: 70px;
  }

  .footer__link {
    margin-right: 58px;
    margin-left: -6px;

    &:hover {
      margin-left: 10px;
    }
  }

  .footer__socials {
    display: none;
  }

  .footer__link-mob {
    padding-top: 70px;
  }

  .footer__link-addr {
    padding-top: 40px;
  }

  .footer__copyright {
    padding-top: 62px;
    top: 0;

    p {
      padding-bottom: 30px;
    }
  }

  .footer {
    padding-top: 59px;
  }
}


@media (max-width: 400px) {
  .footer__top-write h2 {
    width: 335px;
  }

  .footer {
    padding-bottom: 60px;
  }
}
