.header__container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-bottom: 42px;
}

.header__logo-wrapper {
  display: flex;
  align-items: center;
}


.header__logo-wrapper h1 {
  font-family: $fontfamali;
  font-style: normal;
  font-weight: 600;
  font-size: 18px;
  line-height: 23px;
  /* identical to box height */

  letter-spacing: 0.02em;
  text-transform: uppercase;
}

.header__logo-wrapper p {
  font-family: $fontfamali;
  font-style: normal;
  font-weight: normal;
  font-size: 14px;
  line-height: 18px;
  /* identical to box height */
  padding-left: 20px;
  position: relative;
  color: rgba(72, 109, 146, 0.6);
}

.header {
  padding-top: 39px;
  padding-right: 78px;
  padding-left: 78px;
  margin-bottom: 33px;
}

.header__nav-block p {
  font-family: $fontfamali;
  font-style: normal;
  font-weight: normal;
  font-size: 16px;
  line-height: 20px;
  color: #000000;
  padding-right: 5px;

  &:last-child {
    opacity: 0.4;
  }
}

.header__nav-block {
  display: flex;
}

.header__btn {
  position: relative;
  left: 37px;
  font-family: $fontfamali;
  font-style: normal;
  font-weight: normal;
  padding: 22px 47px 21px 46px;
  width: 247px;
  font-size: 18px;
  line-height: 22px;
  border: 1px solid #EBEDF0;
  border-radius: 50px;
  color: transparent;
  -webkit-background-clip: text;
  background-clip: text;
  background-image: -webkit-gradient(linear,left top,right top,from(red),to(#f0f));
  background-image: linear-gradient(to right,red,#f0f);

  &:hover {
    background-image: linear-gradient(to left,red,#f0f);
  }
}

.header__switch {
  position: relative;
  display: inline-block;
  width: 36px;
  height: 24px;
  margin-left: 35px;
  margin-right: 35px;
}


.header__switch input {
  display: none;
}

/* The slider */
.header__slider {
  position: absolute;
  cursor: pointer;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: #ccc;
  -webkit-transition: .4s;
  transition: .4s;
}

.header__slider::before {
  position: absolute;
  content: "";
  height: 18px;
  width: 18px;
  left: 4px;
  bottom: 3px;
  background-color: white;
  transition: 0.4s;
}

.header__switch input:checked + .header__slider {
  background-image: linear-gradient(to right, red, #f0f);
}

.header__switch input:focus + .header__slider {
  box-shadow: 0 0 1px #2196F3;
}

.header__switch input:checked + .header__slider::before {
  transform: translateX(10px);
}

.header__slider.round {
  border-radius: 34px;
}

.header__slider.round::before {
  border-radius: 50%;
}

.header__social-wrapper {
  display: flex;
  align-items: center;
  width: 100%;
  max-width: 492px;

  p {
    font-style: normal;
    font-weight: normal;
    font-size: 18px;
    line-height: 18px;
    position: relative;

    &::before {
      content: "";
      position: absolute;
      width: 25px;
      height: 2px;
      left: 62px;
      top: 6px;
      background-color: black;
    }

    &::after {
      content: "";
      position: absolute;
      width: 25px;
      height: 2px;
      left: 62px;
      top: 12px;
      background-color: black;
    }
  }
}

.header__menu-text {
  margin-right: 39px;
}

@media (max-width: 1100px) {
  .header__switch {
    margin-right: 20px;
    margin-left: 20px;
  }

  .header__nav-block p {
    padding-right: 0;

    &:last-child {
      max-width: 500px;
    }
  }
}

@media (max-width: $tablet-width) {
  .header__switch {
    display: none;
  }

  .header__video-link {
    display: none;
  }

  .header__btn {
    display: none;
  }

  .header__social-wrapper {
    justify-content: flex-end;
    max-width: 150px;
  }
}

@media (max-width: $mobile-width) {
  .header__btn-wrapper {
    display: none;
  }

  .header__nav-block {
    display: none;
  }

  .header__logo-wrapper p {
    display: none;
  }

  .header {
    padding-left: 17px;
    padding-right: 20px;
    padding-top: 20px;
    padding-bottom: 0;
    margin-bottom: 59px;
  }

  .header__container {
    padding-bottom: 0;
  }
}
