.blog {
  margin-top: 17px;
}

.blogpage__views-wrapper svg {
  width: 21px;
  height: 18px;
  fill-opacity: 0.4;
}

.blogpage__header-bg {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  width: 100%;
  height: 532px;
  position: relative;
  background-image: url("../images/content/bg__blog-header.png");
  background-size: cover;
  background-repeat: no-repeat;
  margin-bottom: 30px;
  border-radius: 12px;
  @media 	only screen and (-webkit-min-device-pixel-ratio: 1.5),
    only screen and (min--moz-device-pixel-ratio: 1.5),
    only screen and (-o-min-device-pixel-ratio: 3/2),
    only screen and (min-resolution: $retina-dppx),
    only screen and (min-resolution: $retina-dpi)  {
      background-image: url("../images/content/bg__blog-header@2x.png");
    }

  h2 {
    font-family: $fontfamali;
    font-weight: 500;
    font-size: 50px;
    line-height: 49px;
    color: #fff;
    padding-bottom: 60px;
    padding-left: 60px;
    max-width: 1155px;
  }
}

.blogpage__container {
  padding-left: 78px;
  padding-right: 78px;
}

.blogpage__logo-wrapper {
  display: flex;
  align-items: center;

  &:hover {
    color: rgba(0, 0, 0, 0.4);
    -webkit-background-clip: text;
    background-clip: text;
    background-image: none;
    background-color: rgba(0, 0, 0, 0);
  }

  h3 {
    font-family: $fontfamali;
    font-style: normal;
    font-weight: normal;
    font-size: 22px;
    line-height: 30px;
    color: #333333;
    text-transform: capitalize;
    padding-left: 14px;
    position: relative;
    padding-right: 37px;

    &::before {
      content: "";
      position: absolute;
      width: 12px;
      height: 12px;
      background-image: url("../images/content/marker.svg");
      top: 11px;
      left: 163px;
    }
  }

  p {
    font-family: $fontfamali;
    font-style: normal;
    font-weight: normal;
    font-size: 16px;
    line-height: 20px;
    color: rgba(0, 0, 0, 0.4);
    padding-top: 2px;
  }
}

.blogpage__views-wrapper {
  display: flex;
  align-items: center;

  p {
    font-style: normal;
    font-weight: normal;
    font-size: 16px;
    line-height: 20px;
    padding-right: 10px;
    color: rgba(0, 0, 0, 0.4);
  }
}

.blogpage__info {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-bottom: 60px;
}

.blogpage__header-categories {
  display: flex;
  padding-left: 60px;
  padding-top: 60px;
}

.blogpage__categories-link {
  font-style: normal;
  font-weight: normal;
  font-size: 16px;
  line-height: 20px;
  color: #ffffff;
  padding: 10px;
  padding-left: 20px;
  padding-right: 20px;
  background: linear-gradient(0deg, rgba(255, 255, 255, 0.2), rgba(255, 255, 255, 0.2));
  border: 1px solid rgba(255, 255, 255, 0.2);
  box-sizing: border-box;
  border-radius: 52px;

  &:first-child {
    margin-right: 20px;
  }

  &:hover {
    color: transparent;
    -webkit-background-clip: text;
    background-clip: text;
    background-image: linear-gradient(to right, red, fuchsia);
  }
}


.blogpage__socials {
  display: flex;
  flex-direction: column;
  padding-right: 20px;
  padding-top: 9px;

  svg {
    width: 33px;
    height: 33px;
    background: white;
    padding: 5px;
    border: 1px solid rgba(0, 0, 0, 0.6);
    border-radius: 50%;
    margin-bottom: 21px;

    &:hover {
      fill: #dc26fa;
      border: 1px solid #ff00ff;
    }
  }
}

.blogpage__cont-like {
  display: flex;
  position: relative;
  top: 16px;
  left: 5px;

  img {
    width: 20px;
    height: 18px;
    padding: 0;
    margin-right: 9px;
    fill: red;
  }

  p {
    font-style: normal;
    font-weight: 500;
    font-size: 16px;
    line-height: 20px;

    color: #00A267;
  }
}

.blogpage__main-block {
  display: flex;
  justify-content: space-between;
}

.blogpage__main-text {
  width: 100%;
  max-width: 1258px;

  p {
    font-style: normal;
    font-weight: normal;
    font-size: 26px;
    line-height: 40px;
    word-wrap: break-word;
    color: #333333;
    padding-bottom: 40px;
  }

  img {
    padding-top: 59px;
    padding-bottom: 60px;
  }

  h3 {
    font-style: normal;
    font-weight: 500;
    font-size: 90px;
    line-height: 100px;
    max-width: 846px;
    padding-bottom: 60px;
  }
}

.blogpage__cat-text p {
  padding-bottom: 0;
}

.blogpage__text-imgs {
  display: flex;
  width: 100%;
  padding-top: 59px;

  img {
    object-fit: cover;
    height: 100%;
    padding: 0;
  }
}

.blogpage__text-img {
  margin-right: 16px;
  padding: 0;
  margin-bottom: 60px;
  width: 100%;
  max-width: 621px;
  object-fit: cover;
  height: 100%;

  &:last-child {
    margin-right: 0;
  }
}


.blogpage__list {
  display: flex;
  padding-bottom: 50px;
  justify-content: space-between;
}


.blogpage__recomendtion {
  padding-right: 80px;
  padding-left: 80px;
  background: #F2F2F2;
  margin-top: 20px;

  h2 {
    font-style: normal;
    font-weight: 500;
    font-size: 90px;
    line-height: 100px;
    padding-top: 60px;
    padding-bottom: 60px;
  }
}

.blogpage__img-wrap {
  img {
    border-radius: 12px;
  }
}

.blogpage__sup-text {
  margin-bottom: 20px;
}

.blogpage__social-wrap {
  display: flex;
  flex-direction: column;

  svg {
    width: 33px;
    height: 33px;
    background: white;
    padding: 5px;
    border: 1px solid rgba(0, 0, 0, 0.6);
    border-radius: 50%;
    margin-bottom: 21px;

    &:hover {
      fill: #dc26fa;
      border: 1px solid #ff00ff;
    }
  }
}


@media (max-width: 1220px) {
  .blogpage__recomendtion {
    padding-right: 30px;
    padding-left: 30px;
  }
}

@media (max-width: $tablet-width) {
  .blogpage__recomendtion {
    padding-right: 5px;
    padding-left: 5px;
  }

  .blogpage__list {
    width: 100%;
    display: flex;
    justify-content: space-between;
  }
}

@media (max-width: $mobile-width) {
  .blogpage__header-categories {
    padding-left: 14px;
    padding-top: 14px;

    a {
      font-style: normal;
      font-weight: normal;
      font-size: 12px;
      line-height: 15px;

      &:first-child {
        margin-right: 14px;
      }
    }
  }

  .blogpage__header-bg {
    height: 200px;
    margin-bottom: 17px;
    background-image: url("../images/content/bg__blog-header-mob.png");

    @media 	only screen and (-webkit-min-device-pixel-ratio: 1.5),
    only screen and (min--moz-device-pixel-ratio: 1.5),
    only screen and (-o-min-device-pixel-ratio: 3/2),
    only screen and (min-resolution: $retina-dppx),
    only screen and (min-resolution: $retina-dpi)  {
      background-image: url("../images/content/bg__blog-header-mob@2x.png");
    }

    h2 {
      font-style: normal;
      font-weight: 500;
      font-size: 28px;
      line-height: 36px;
      color: #000000;
    }
  }

  .blogpage__info {
    flex-direction: column;
    align-items: flex-start;
    padding-bottom: 22px;
  }

  .blogpage__views-wrapper {
    justify-content: flex-end;
    padding-right: 14px;
    padding-bottom: 14px;

    svg {
      fill: #ffffff;
      fill-opacity: 1;
    }
  }

  .blogpage__views p {
    font-style: normal;
    font-weight: normal;
    font-size: 16px;
    line-height: 20px;
    color: #ffffff;
  }

  .blogpage__logo-wrapper {
    padding-bottom: 17px;

    img {
      width: 30px;
      height: 30px;
    }

    h3 {
      font-style: normal;
      font-weight: normal;
      font-size: 16px;
      line-height: 24px;
      padding-left: 10px;

      &::before {
        top: 8px;
        left: 120px;
      }
    }

    p {
      font-style: normal;
      font-weight: normal;
      font-size: 14px;
      line-height: 18px;
      padding-top: 4px;
    }
  }

  .blogpage__container {
    padding-left: 20px;
    padding-right: 20px;
  }

  .blogpage__main-block {
    flex-direction: column;
  }

  .blogpage__socials {
    display: flex;
    flex-direction: row;
    padding-bottom: 9px;

    svg {
      margin-right: 23px;
    }
  }

  .blogpage__social-wrap {
    display: flex;
    flex-direction: row-reverse;
  }

  .blogpage__cont-like {
      position: relative;
      top: 8px;
      left: 5px;
      padding-top: 0;

    svg {
      margin-right: 9px;
    }
  }

  .blogpage__main-text {
    p {
      font-style: normal;
      font-weight: normal;
      font-size: 16px;
      line-height: 24px;
      padding-bottom: 24px;
    }

    img {
      height: 224px;
      padding: 0;
      max-width: 535px;
      width: 100%;
      margin: 0 auto;
      object-fit: cover;
      margin-top: 28px;
    }

    h3 {
      font-style: normal;
      font-weight: 500;
      font-size: 28px;
      line-height: 36px;
      padding-top: 30px;
      padding-bottom: 30px;
    }
  }

  .blogpage__cat-text p {
    padding-bottom: 0;
  }

  .blogpage__text-imgs {
    display: flex;
    flex-direction: column;
    padding-top: 32px;

    img {
      border-radius: 12px;
      width: 100%;
      margin: 0;
      height: 188px;
      object-fit: cover;
      max-width: 800px;
    }
  }

  .blogpage__text-img {
    margin-bottom: 20px;
    display: flex;
  }

  .blogpage__recomendtion {
    padding-left: 20px;
    padding-right: 20px;
    margin-top: 36px;

    h2 {
      font-style: normal;
      font-weight: 500;
      font-size: 28px;
      line-height: 36px;
      padding-top: 30px;
      padding-bottom: 30px;
    }
  }

  .blogpage__list {
    flex-direction: column;
    padding-bottom: 29px;
  }

  .blogpage__categories-link {
    padding-left: 12px;
    padding-right: 12px;
    padding-bottom: 8px;
    padding-top: 8px;
  }

  .blogpage__info h2 {
    font-style: normal;
    font-weight: 500;
    font-size: 28px;
    line-height: 36px;
    text-align: left;
  }

  .blogpage__sup-text {
    margin-bottom: 0;
  }
}


@media (max-width: 400px) {
  .blogpage__main-text {
    img {
      object-fit: cover;
      height: 124px;
    }
  }

  .blogpage__text-imgs {
    object-fit: contain;
    padding-bottom: 11px;

    img {
      height: 188px;
    }
  }
}
