@mixin font-face($font-name, $path, $weight: normal, $style: normal, $option: fallback) {
  @font-face {
    font-family: quote($font-name);
    src:
      url(#{$path}.woff2) format("woff2"),
      url(#{$path}.woff) format("woff");
    font-weight: $weight;
    font-style: $style;
    font-display: $option;
  }
}

@include font-face('EuclidCircularB', '../fonts/EuclidCircularB/EuclidCircularB', 400, 'normal', 'fallback');
@include font-face('EuclidCircularB', '../fonts/EuclidCircularB-light/EuclidCircularB-light', 300, 'normal', 'fallback');
@include font-face('EuclidCircularB', '../fonts/EuclidCircularB-bold/EuclidCircularB-Bold', 500, 'normal', 'fallback');
