.blog__item-wrapper {
  padding-left: 30px;

  h4 {
    font-style: normal;
    font-weight: 500;
    font-size: 40px;
    line-height: 50px;
    padding-bottom: 20px;
    max-width: 654px;
  }

  p {
    font-style: normal;
    font-weight: normal;
    font-size: 16px;
    line-height: 24px;
    padding-bottom: 30px;
    padding-right: 10px;
  }
}

.blog__logo-item {
  padding-top: 30px;
  padding-bottom: 20px;
  display: flex;
  align-items: center;

  p {
    padding-bottom: 0;
  }
}

.blogpage__item-link {
  font-style: normal;
  font-weight: normal;
  font-size: 18px;
  line-height: 23px;
  color: transparent;
  position: relative;
  -webkit-background-clip: text;
  background-clip: text;
  background-image: linear-gradient(to right, red, fuchsia);

  &:hover {
    &::before {
      content: "\2192";
      position: absolute;
      width: 10px;
      height: 10px;
      color: #dc26fa;
      left: 137px;
    }
  }
}

.blog__item-footer {
  display: flex;
  justify-content: space-between;
  padding-top: 23px;
  padding-bottom: 18px;
  padding-left: 21px;
  padding-right: 23px;
}

.blog__item {
  width: 100%;
  max-width: 714px;
  margin-right: 16px;
  background-color: #ffffff;
  border-radius: 12px;

  &:last-child {
    margin-right: 0;
  }
}

.blog__cont-like {
  top: 1px;
  left: 3px;
}

@media (max-width: 1220px) {
  .blog__item-wrapper {
    padding-left: 20px;
    padding-right: 20px;

    h4 {
      font-size: 32px;
      padding-right: 5px;
    }

    .blogpage__header-bg h2 {
      padding-right: 20px;
    }
  }
}

@media (max-width: $tablet-width) {
  .blog__item img {
    height: 280px;
  }

  .blog__logo-item img {
    height: 40px;
  }

  .blogpage__cont-like img {
    height: 18px;
  }
}

@media (max-width: $mobile-width) {
  .blog__item {
    margin-bottom: 20px;

    img {
      width: 100%;
      margin: 0 auto;
    }
  }

  .blog__item-wrapper {
    padding-right: 9px;

    h4 {
      font-style: normal;
      font-weight: 500;
      font-size: 28px;
      line-height: 36px;
    }
  }

  .blog__logo-item {
    padding-top: 19px;

    img {
      width: 30px;
      height: 30px;
      margin: 0;
    }

    h3 {
      font-style: normal;
      font-weight: normal;
      font-size: 16px;
      line-height: 24px;

      &::before {
        top: 7px;
        left: 123px;
      }
    }

    p {
      font-style: normal;
      font-weight: normal;
      font-size: 14px;
      line-height: 18px;
    }
  }

  .blog__like-wrap {
    margin-right: 9px;
  }
}


@media (max-width: 400px) {
  .blog__item img {
    height: 164px;
    width: 100%;
    max-width: 400px;
  }

  .blog__like-wrap {
    margin-right: 9px;

    img {
      height: 18px;
    }
  }

  .blog__logo-item {
    img {
      width: 30px;
      height: 30px;
      margin: 0;
    }
  }
}
