.vcru__imgs {
  display: flex;
  position: relative;
  margin-left: -10px;

  &::before {
    content: "+101";
    position: absolute;
    right: -3px;
    top: -22px;
    color: #00a267;
  }

  img {
    margin-right: 30px;

    &:hover {
      transition: 0.7s;
      transform: scale(1.2);
    }
  }
}

.vcru {
  display: flex;
  justify-content: center;
  background-color: #FEEBEF;
  padding-top: 69px;
  padding-bottom: 45px;
}

.vcru__link {
  margin-right: 70px;
  position: relative;
  left: 13px;
}

.vcru__main-link {
  font-style: normal;
  font-weight: normal;
  font-size: 26px;
  line-height: 33px;
  color: transparent;
  -webkit-background-clip: text;
  background-clip: text;
  background-image: linear-gradient(to right, red, fuchsia);
}

.vcru__link-wrap {
  display: flex;
  align-items: flex-end;
}

@media (max-width: 1220px) {
  .vcru {
    flex-wrap: wrap;
  }

  .vcru__link-wrap {
    margin-bottom: 40px;
  }
}

@media (max-width: $mobile-width) {
  .vcru__imgs {
    picture {
      &:first-child {
        display: none;
      }
    }

    img {
      &:first-child {
        display: none;
      }
    }
  }

  .vcru {
    padding-top: 30px;
    padding-bottom: 30px;
    padding-left: 20px;
    padding-right: 20px;
  }
}


@media (max-width: 400px) {
  .vcru__link {
    margin: 0 auto;
    left: 8px;
  }

  .vcru__imgs {
    margin-left: 22px;
    margin-top: 13px;
    justify-content: space-between;
    width: 100%;
    max-width: 336px;

    &::before {
      right: 3px;
    }

    img {
      margin-right: 0;
    }
  }
}
